.movie-card {
  background-color: #000;
  color: #bbb;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 8px 0; }

.movie-card__poster-container {
  position: relative;
  padding-bottom: 150%;
  height: 0;
  width: 100%;
  max-width: 500px; }

.movie-card__poster {
  position: absolute;
  width: 100%;
  overflow: hidden; }

.movie-card__info {
  padding: 16px;
  text-align: center;
  text-transform: uppercase;
  overflow-wrap: break-word; }

.movie-card__title {
  font-size: 19px;
  padding: 0 0 8px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400; }

.movie-card__genres {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 11px; }

.movies__none {
  padding: 0 4px 16px;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 13px;
  grid-column: 2 / 5; }

.genre-filter-item {
  list-style-type: none;
  padding: 2px;
  margin: 0; }

.genre-filter-item__checkbox input[type='checkbox'] {
  opacity: 0;
  cursor: pointer;
  position: absolute; }

.genre-filter-item__text {
  background-color: #2a1a1f;
  padding: 4px;
  box-sizing: content-box;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 11px;
  cursor: pointer; }
  .genre-filter-item__text:hover {
    background-color: #3a242b; }

input[type=checkbox]:checked ~ .genre-filter-item__text {
  background: #ad8350; }

input[type=checkbox]:focus ~ .genre-filter-item__text,
input[type=checkbox]:active ~ .genre-filter-item__text {
  padding: 3px;
  border: .5px dotted #fff; }

.genre-filter {
  padding: 0 0 16px; }

.genre-filter__heading {
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 13px;
  padding: 0 0 4px 4px; }

.rating-filter {
  padding: 0 0 16px;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 13px; }

.rating-filter__heading {
  font-weight: 700;
  padding: 0 0 4px 4px;
  font-size: 13px; }

.rating-filter__select {
  -moz-appearance: none;
       appearance: none;
  -webkit-appearance: none;
  background-color: #2a1a1f;
  color: #bbb;
  border: none;
  cursor: pointer;
  width: 100%;
  padding: 4px; }
  .rating-filter__select:hover {
    background-color: #3a242b; }

.rating-filter__option {
  -moz-appearance: none;
       appearance: none;
  -webkit-appearance: none; }

.layout {
  background-color: #111;
  color: #bbb;
  min-height: 100vh;
  padding: 16px 0; }

.layout--loading {
  margin: auto;
  grid-column: span 99; }

@media screen and (min-width: 450px) {
  .layout {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 8px;
    padding: 16px; } }

@media screen and (min-width: 600px) {
  .layout {
    grid-template-columns: 1fr 1fr 1fr; } }

@media screen and (min-width: 800px) {
  .layout {
    grid-template-columns: 1fr 1fr 1fr 1fr; } }

@media screen and (min-width: 450px) {
  .layout__sidebar {
    grid-column: 1;
    grid-row: span 99; }
  .layout__sidebar-inner {
    position: -webkit-sticky;
    position: sticky;
    top: 0; } }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
